h1, h2 {
	font-family: 'Playfair Display', serif;
}
h1, h2, h3, h4 {
	margin:0;
	
}

h1 {
	font-size: 20rem;
}

h2{
	font-size: 10rem;
}

h3{
	font-size: 2.6rem;
}

h4 {
	font-size: 2.2rem;
}

a {
	color: white;
	text-decoration: none;
	&:hover {
		color: rgba(255, 145, 115, 1);
	}
}

/* paragraphs */

p {
	line-height: 1.5;
	font-size: 1.6rem;
}

/*blockquotes*/

blockquote {
	background: #F7F7F7;
	margin: 0;
	padding: 20px;
	p {
		margin-top: 0;
	}
}

/*tables*/

table {
	td {
		padding: 10px;
	}
	tr td:first {
		padding-right: 0;
	}
}

/*def lists*/

dt {
	font-weight: 600;
	padding-bottom: 5px;
}

dd {
	padding: 10px;
	margin-left: 10px;
}

/*lists*/

ul li, ol li {
	padding: 3px;
}

/*PRE*/

pre {
	background: #F7F7F7;
	padding: 10px;
	border-radius: 4px;
	border: 1px solid #ccc;
}


/*
====================================
Global Classes / Wordpress Specific
====================================
*/

.aligncenter {
	text-align: center;
	margin: 0 auto;
}

.alignleft {
	float: left;
}

.alignright {
	float: right;
}

img {
	&.alignleft {
		padding-right: 5px;
	}
	&.alignright {
		padding-left: 5px;
	}
}

.clear {
	width: 100%;
	float: left;
	clear: both;
}